<template>
	<view class="container">
		<view class="top-box">
			<uni-row>
				<uni-col :span="5">
					<view class="demo-uni-col dark">
						<image :src="userInfo.avatar?userInfo.avatar:'../../static/images/user.jpg'" class="avatar"></image>
					</view>
				</uni-col>
				<uni-col :span="19">
					<view class="demo-uni-col light">
						<button class="login-btn" v-if="!isAuth" @click="navTo">点击登录</button>
						<view class="info" v-else>
							<view class="username">{{userInfo.username}}</view>
							<view class="email">{{userInfo.email}}</view>
						</view>
					</view>
				</uni-col>
			</uni-row>
		</view>
		<view class="menu-box">
			<uni-list class="list">
				<uni-list-item :clickable='true' @click="navTo('/pages/order/order')" :show-extra-icon="true" showArrow :extra-icon="extraIconList[0]" title="订单管理" />
				<uni-list-item :clickable='true' @click="navTo('/pages/user/address')" :show-extra-icon="true" showArrow :extra-icon="extraIconList[1]" title="地址管理" />
				<uni-list-item :clickable='true' @click="navTo('/pages/goods/collect')" :show-extra-icon="true" showArrow :extra-icon="extraIconList[2]" title="我的收藏" />
				<uni-list-item :clickable='true' @click="navTo('/pages/common/set')" :show-extra-icon="true" showArrow :extra-icon="extraIconList[3]" title="设置"/>
			</uni-list>
		</view>
	</view>
</template>

<script>
	import {
		mapState
	} from 'vuex'
	export default {
		data() {
			return {
				extraIconList: [{
						color: '#7CC58E',
						size: '24',
						type: 'shop-filled'
					},
					{
						color: '#5FBEA0',
						size: '24',
						type: 'location-filled'
					},
					{
						color: '#49AFF3',
						size: '24',
						type: 'star-filled'
					},
					{
						color: '#EA7E75',
						size: '24',
						type: 'gear-filled'
					}
				],
			}
		},
		onLoad: function() {

		},
		computed: {
			...mapState(['isAuth', 'userInfo'])
		},
		methods: {
			navTo(url) {
				if (this.isAuth) {
					uni.navigateTo({
						url: url
					})
				} else {
					uni.navigateTo({
						url: '/pages/user/login'
					})
				}
			}
		}
	}
</script>

<style lang="scss">
	.container {
		.top-box {
			width: 100%;
			height: 350upx;
			background-image: linear-gradient(to right, #5DD699, #57BF8B);
			padding: 150upx 40upx 0;
			position: relative;

			&::after {
				content: '';
				width: 100%;
				height: 40upx;
				background-image: linear-gradient(to right, #5DD699, #57BF8B);
				position: absolute;
				bottom: -12px;
				left: 0;
				border-radius: 0 0 150% 150%;
			}

			.demo-uni-col {
				height: 120upx;

				.avatar {
					width: 120upx;
					height: 120upx;
					border: 6upx solid $uni-border-1;
					border-radius: 50%;
				}

				.login-btn {
					font-size: 30upx;
					text-align: left;
					height: 100%;
					line-height: 120upx;
					background-color: transparent;
					color: #fff;
				}

				.info {
					height: 100%;
					display: flex;
					flex-direction: column;
					justify-content: center;
					padding: 0 28upx;
					line-height: 42upx;
					color: #fff;
					font-size: 24upx;

					.username {
						font-size: 30upx;
					}
				}
			}
		}

		.menu-box {
			padding: 0 35upx;
			margin-top: 50upx;
		}
	}
</style>